import{ createRouter, createWebHashHistory } from'vue-router';
import Gallery from './components/Gallery.vue';
import MyHome from './components/MyHome.vue';
import Kyungbok from './components/Kyungbok.vue';
import Sejong from './components/Sejong.vue';
import Insadong from './components/Insadong.vue';
import Hwasung from './components/Hwasung.vue';
import Everland from './components/Everland.vue';
import Park from './components/Park.vue';
import Cafe from './components/Cafe.vue';
import Egypt from './components/Egypt.vue';

const routes = [
    {
        path: "/gallery",
        component: Gallery,
    },
    {
        path: "/myhome",
        component: MyHome,
    },
    {
        path: "/kyungbok",
        component: Kyungbok,
    },
    {
        path: "/sejong",
        component: Sejong,
    },
    {
        path: "/insadong",
        component: Insadong,
    },
    {
        path: "/hwasung",
        component: Hwasung,
    },
    {
        path:"/everland",
        component: Everland,
    },
    {
        path: "/park",
        component: Park,
    },
    {
        path: "/cafe",
        component: Cafe,
    },
    {
        path: "/egypt",
        component: Egypt,
    }
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
